import Home from '../pages/Home/Home.vue'
export default [{
    path: '/home',
    name: 'home',
    component: Home,
  },
  {
    path: '/case',
    name: 'case',
    component: (resolve) => {
    require(['../pages/Case/Case.vue'], resolve)
    },
  },
  {
    path: '/routine',
    name: 'routine',
     component: (resolve) => {
       require(['../pages/Routine/Routine.vue'], resolve)
     },
  },
  {
    path: '/structure',
     name: 'structure',
      component: (resolve) => {
        require(['../pages/Structure/Structure.vue'], resolve)
      },
  },
  {
    path: '/learn',
     name: 'learn',
      component: (resolve) => {
        require(['../pages/Learn/Learn.vue'], resolve)
      },
  },
  {
    path: '/solve',
     name: 'solve',
      component: (resolve) => {
        require(['../pages/Solve/Solve.vue'], resolve)
      },
  },
  {
    path: '/plug',
     name: 'plug',
      component: (resolve) => {
        require(['../pages/Plug/Plug.vue'], resolve)
      },
  },
    {
      path: '/test',
       name: 'test',
         component: (resolve) => {
           require(['../pages/Test/Test.vue'], resolve)
         },
    },
  {
    path: '/:id',
    redirect: '/home'
  },
   {
     path: '/',
     redirect: '/home'
   }
]